export default {
  name: "Slider",
  data() {
    return {
      images: [
        require('@/assets/picture/chambres_hote.jpg'),
        require('@/assets/picture/chambre_hote_Normandie.jpg'),
        require('@/assets/picture/cuisine.jpeg'),
        require('@/assets/picture/jardin.jpeg'),
        require('@/assets/picture/vue_Mont_St_Michel.jpeg'),
      ],
      timer: null,
      currentIndex: 0
    };
  },

  mounted: function() {
    this.startSlide();
  },

  methods: {
    startSlide: function() {
      this.timer = setInterval(this.next, 4500);
    },
    next: function() {
      this.currentIndex += 1;
    },
    prev: function() {
      this.currentIndex -= 1;
    },
    stop: function() {
      clearInterval(this.timer);
    }
  },

  computed: {
    currentImg: function() {
      return this.images[Math.abs(this.currentIndex) % this.images.length];
    }
  }
};
